import AsyncStorage from '@react-native-async-storage/async-storage';
import { createStore } from 'aniuta';
import React, { useState } from 'react';

import { API } from '../services/index';

const userAuth = createStore({
   name: 'AuthService',
   Store: () => {
      const initialState = { token: null };
      const [authState, setAuthState] = useState({ ...initialState });
      const [isLoggedIn, setIsLoggedIn] = useState(false);

      const getToken = async () => {
         const gotAuthAsyncToken = await AsyncStorage.getItem('tokenAuth');
         if (gotAuthAsyncToken) {
            const { token } = JSON.parse(gotAuthAsyncToken);
            setAuthState({ token });
            setIsLoggedIn(true);
         }
      };

      const signIn = async ({ name, password }) => {
         // console.log('name', name);
         // console.log('password', password);
         API.signIn({
            data: { name, password },
         })
            .then((response) => {
               if (response.data.hasOwnProperty('token')) {
                  const { token } = response.data;
                  AsyncStorage.setItem('tokenAuth', JSON.stringify({ token }));
                  setAuthState({ token });
                  setIsLoggedIn(true);
               } else {
                  setAuthState({ ...initialState });
               }
            })
            .catch((error) => {
               alert(error);
            });
      };

      const signUp = async ({ name, password }) => {
         API.signUp({
            data: { name, password },
         })
            .then((response) => {
               console.log('signUp', response);
            })
            .catch((error) => alert(error));
      };

      const signOut = () => {
         AsyncStorage.removeItem('tokenAuth');
         setAuthState({ ...initialState });
         setIsLoggedIn(false);
      };

      return {
         ...authState,
         signIn,
         signUp,
         isLoggedIn,
         authState,
         getToken,
         signOut,
      };
   },
});

export default userAuth;
